import React, { useEffect, useMemo, useState } from 'react';
import axios from 'axios';

import DataTable, { createTheme } from 'react-data-table-component';

export default function CreateForm(props) {
    const [forms, setForms] = useState([])

    const customStyles = {
        rows: {
          style: {
            minHeight: '80px', // override the row height
            fontSize:'17px',
          }
        },
        headCells: {
          style: {
            paddingLeft: '8px', // override the cell padding for head cells
            paddingRight: '8px',
          },
        },
        cells: {
          style: {
            paddingLeft: '8px', // override the cell padding for data cells
            paddingRight: '8px',
          },
        },
      };

    useEffect(() => {
        axios.get(`/form/list`).then(res =>{
            console.log(res.data)
            setForms(res.data)
        });
    }, []);

    const handleDelete=(form)=> {
        // console.log(row)
        console.log("Handle delete")
        if (window.confirm("Are u Sure you want delete it ?"))
        axios.delete(`/form/delete/${form._id}`).then(res=>{
            setForms(forms.filter(item=>{
                return item._id !== form._id
            }))
        }).catch(err=>{
            alert("Something Wrong")
        })

    };

    const handleShow=(form)=> {
        props.history.push(`/form/display/${form._id}`);
 
    };
    const handlePublish = (form)=>{
        axios.put(`/form/${ form._id}/publish`,{}).then(res=>{
            setForms(forms.filter(item=>{
                 if(item._id === form._id){
                     item.published = res.data.published
                 }
                 return true
            }))
        }).catch(err=>{
            alert("Something Wrong")
        })
    }

    const handleEdit=(form)=> {
        axios.get(`/form/${ form._id}`).then(res=>{
        
        props.history.push(`/form/edit/${form._id}`,{form:res.data.form});
            
        }).catch(err=>{
            alert("Not Found")
        })
        
    };
    
    const columns = useMemo(
        () => [ 
        { name: "Title", selector: "form.title", sortable: true },
        {
            name:"Actions",
            grow:2,
            center:true,
            compact:true,
            wrap:true,
            style:{
                
            },
            cell: (row,index,column,id) =>
            <> 
             <button style={{'marginRight':'10px',"padding":"10px 16px","border":"none","background":"gray","color":"white","borderRadius":"50em","fontSize":"14px"}} onClick={()=>handleShow(row)}>Show</button>
             <button style={{'marginRight':'10px',"padding":"10px 16px","border":"none","background":"gray","color":"white","borderRadius":"50em","fontSize":"14px"}} onClick={()=>handlePublish(row)}>{row.published?'Un-Publish':'Publish'}</button>
             <button style={{'marginRight':'10px',"padding":"10px 16px","border":"none","background":"#007bff","color":"white","borderRadius":"50em","fontSize":"14px"}} onClick={()=>handleEdit(row)}>Edit</button>
             <button  style={{"padding":"10px 16px","border":"none","background":"#dc3545","color":"white","borderRadius":"50em","fontSize":"14px"}} onClick={()=>handleDelete(row)}>Delete</button>
            </>
        },
      ])

    return (
        <div>
            <DataTable
                    title="Forms"
                    columns={columns}
                    data={forms}
                    defaultSortFieldId={1}
                    pagination
                    highlightOnHover
                    striped
                    pointerOnHover
                    customStyles={customStyles}
                    />

        </div>
    );
  
}